.password-sharing-page .features .row1{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    margin:7.5% 0;
    padding:0 10%;
    .info1{
        width: 40%;
    }
    .img{
        width: 40%;
        border-radius: 20px;
    }
    h1{
        font-size: 35px;
        margin-bottom: 15px;
    }
    p{
        font-size: 18px;
        margin:20px 0;
    }
    .img1{
        content: url('../../../../assets/PasswordSharing/img1.png');
    }
    .img2{
        content: url('../../../../assets/PasswordSharing/img2.png');
    }
    .img3{
        content: url('../../../../assets/PasswordSharing/img3.png');
    }
    .link{
        margin-top: 45px;
    }
    a{
        background: #427cff;
        color:#fff;
        font-size: 18px;
        padding:13px 35px;
        border-radius: 25px;
    }
}

@media(max-width:1401px){
    .password-sharing-page .features .row1{
        padding:0 10%;
        .info1{
            width: 45%;
        }
        .img{
            width: 42.5%;
        }
        h1{
            font-size: 27.5px;
            margin-bottom: 15px;
        }
        p{
            font-size: 15px;
            margin:20px 0;
        }
        .link{
            margin-top: 30px;
        }
        a{
            font-size: 15px;
            padding:12px 30px;
        }
    }
}

@media(max-width:1401px){
    .password-sharing-page .features .row1{
        padding:0 5%;
        .info1{
            width: 47.5%;
        }
    }
}

@media(max-width:767px){
    .password-sharing-page .features .row1{
        padding:0 7.5%;
        margin:30px 0;
        .info1{
            width: 100%;
            margin:20px 0;
        }
        .img{
            width: 350px;
            max-width: 100%;
            margin:20px auto;
        }
        h1{
            font-size: 22.5px;
            margin-bottom: 10px;
        }
        p{
            margin:15px 0;
        }
        .link{
            margin-top: 25px;
        }
    }
}

.password-sharing-page .features .row1{
    a{
        display: inline-block;
        vertical-align: middle;
        -webkit-transform: perspective(1px) translateZ(0);
        transform: perspective(1px) translateZ(0);
        box-shadow: 0 0 1px rgba(0, 0, 0, 0);
        position: relative;
        -webkit-transition-property: color;
        transition-property: color;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
    }
    a:before {
        content: "";
        position: absolute;
        z-index: -1;
        top: -1px;
        bottom: -1px;
        left: -1px;
        right: -1px;
        border-radius: 25px;
        background: #fff;
        -webkit-transform: scaleX(0);
        transform: scaleX(0);
        -webkit-transform-origin: 50%;
        transform-origin: 50%;
        -webkit-transition-property: transform;
        transition-property: transform;
        -webkit-transition-duration: 0.3s;
        transition-duration: 0.3s;
        -webkit-transition-timing-function: ease-out;
        transition-timing-function: ease-out;
    }
    a:hover, a:focus, a:active {
        color: #265bd6;
    }
    a:hover:before, a:focus:before, a:active:before {
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}